import React from 'react';

import Paragraphs from './Paragraphs';


const ParagraphList = ({ Ghostsexplorepage }) => {return( <div>{
    Ghostsexplorepage && Ghostsexplorepage.map((user,i) =>{return(
        <Paragraphs
        key= {i}
        entity= {Ghostsexplorepage[i].entity}
        action = {Ghostsexplorepage[i].action}
       />
    );}  )
}</div>

)};

export default ParagraphList;