import React from 'react';
import {Link} from 'react-router-dom';







 const Box =({name,image,event}) => {
    
    return(  
    <div className='tc bg-light-blue w-30 h-20 dib br8 pa2 ma3 grow   bw2 shadow-5  mr2 mb1 ' >

        <div>
            <h2 >{name}</h2>

        {image} 
       
       
         
        
        <Link to= '/app2'> {event} </Link>

        </div>



    </div> 


);}

export default Box;



