import React, {Component} from 'react';
import ParagraphList from '../Components/ParagraphList';
import {Ghostsexplorepage} from '../Containers/Ghostsexplorepage';
import Searchbar from '../Components/Searchbar';
import Scroll from '../Components/Scroll';



class App2 extends Component { constructor (){
    super()
    this.state={ Ghostsexplorepage:Ghostsexplorepage, searchview: ''

    }

    

}

searchChange = (event) =>{
    this.setState({searchview:event.target.value})}



render() {

    const filteredGhostsParagraph= this.state.Ghostsexplorepage.filter(Ghostsexplorepage =>{return Ghostsexplorepage.entity.toLowerCase().includes(this.state.searchview.toLowerCase())})

return(
<div className = 'tc'>
    <h1 className ='tc'>Enjoy your journey past the vale</h1>
    <Searchbar searchChanger={this.searchChange}/>
   <Scroll> <ParagraphList Ghosts = {filteredGhostsParagraph}/></Scroll>
    </div>
);}}

export default App2;